<template>
  <div id="app">
    <!-- 使用 router-view 把页面交给 vue-router -->
    <ScrollNav v-show="showScroll" :class="[scrolled && $route.name !== 'course' ? 'scrolled' : '' ]" class="scrollNav"></ScrollNav>
    <router-view></router-view>
  </div>
</template>

<script>
import ScrollNav from '@/components/common/scrollNav.vue'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'app',
  components: {
    ScrollNav
  },
  computed: {
    ...mapState({
      scrolled: state => state.scrollNav.currentScrollValue > 50
    }),
        showScroll: function () {
        if (this.$route.name == 'home') {
          if(this.scrolled) {
            return true
          }
          return false
        }

        return true
    }
  },
  async created() {
    if (this.loginState) {
        let res = await userApi.getUserInfo()
        this.changeUserInfo(res.data)
    }
  },
  methods: {
    ...mapActions({
      updateCurrentScrollValue: 'scrollNav/updateCurrentScrollValue'
    })
  },

  mounted() {
    window.addEventListener('scroll', this.updateCurrentScrollValue)
  }
}
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
ul {
  list-style-type: none;
}

img {
  height: 100%;
}
.scrolled {
  position: fixed;
  top: 0;
}

.scrollNav {
  box-shadow: 0 0 16px 0 rgba(0,0,0,.18);
  margin-bottom: 20px;
}
</style>