const state = {
    currentScrollValue: 0
  };
  
  const mutations = {
    updateCurrentScrollValue(state, data) {
      state.currentScrollValue = data;
    }
  };
  
  const actions = {
    updateCurrentScrollValue(context) {
      let scrollTop =
        document.documentElement.scrollTop ||
        window.pageYOffset ||
        document.body.scrollTop;
  
      context.commit("updateCurrentScrollValue", scrollTop);
    }
  };
  
  export default {
    namespaced: true,
    state,
    mutations,
    actions
  };