// 导入 Vue 及 vue-router
import Vue from "vue";
import Router from "vue-router";

// 这是 Vue 中使用插件的固定模式，
// 我们之后还会与他见面。
Vue.use(Router);

// 导入这个路由下使用的组件
// 我们还没有写首页，就用 Vue 的欢迎界面吧。
// @ 指向的目录是 src.
// 懒加载，可以用你自己实现的那个
const __import__ = file => () => import(`@/pages/${file}.vue`);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "home",
      // 在这里添加导入的组件，可以用你自己实现的懒加载函数
      component: __import__("home"),
      meta: {
        title: "在线做实验，高效学编程 - 实验楼"
      }
    },{
      path: '/courses',
      name: 'courses',
      component: __import__('courses'),
      meta: {
          title: '精选项目课程_IT 热门课程_实验楼课程 - 实验楼 - 实验楼'
      }
  },
  {
    path: '/course/:id',
    name: 'course',
    component: __import__('course'),
    meta: {
        title: '- 实验楼'
    }
}
  ]
});

router.afterEach((to) => {
  // 设置当前页面的 title 为 meta 中我们设置的 title.
  document.title = to.meta.title
})

// 导出我们刚刚配置好的路由信息
export default router;